.LoadingIndicator_Loading_Indicator__1KMH5 {
    display: inline-block;
    position: relative;
    width: 54px;
    height: 54px;
  }
  .LoadingIndicator_Loading_Indicator__1KMH5 div {
    display: block;
    position: absolute;
    width: 44px;
    height: 44px;
    margin: 6px;
    border: 6px solid #ff2058;
    border-radius: 50%;
    animation: LoadingIndicator_Loading_Indicator__1KMH5 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    border-color: #ff2058 transparent transparent transparent;
  }
  .LoadingIndicator_Loading_Indicator__1KMH5 div:nth-child(1) {
    animation-delay: -0.45s;
  }
  .LoadingIndicator_Loading_Indicator__1KMH5 div:nth-child(2) {
    animation-delay: -0.3s;
  }
  .LoadingIndicator_Loading_Indicator__1KMH5 div:nth-child(3) {
    animation-delay: -0.15s;
  }
  @keyframes LoadingIndicator_Loading_Indicator__1KMH5 {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  
